@font-face {
	font-family: 'Poppins-Bold';
	src: url('../fonts/Poppins-Bold.eot');
	src: url('../fonts/Poppins-Bold.woff') format('woff'),
		url('../fonts/Poppins-Bold.ttf') format('truetype');
	font-weight: bold;
	font-style: normal;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Poppins-Bold';
}

.header {
	background: url(../img/1.jpg) no-repeat center center/cover;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
	position: relative;
	overflow-x: hidden;
}

.win {
	font-size: 5rem;
	color: #fff;
	text-shadow: 2px 2px 7px rgba(0,0,0,0.7);
	background-color: rgba(0,0,0,0.5);
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	width: 100%;
	height: 100%;
	z-index: 5;
	transform: scale(0);
	transition: all .5s ease;
	user-select: none;
}

.records {
	color: #fff;
	background-color: rgba(0,0,0,0.8);
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	position: absolute;
	top: 0;
	right: 0;
	width: 350px;
	height: 100%;
	z-index: 20;
	transform: translateX(350px);
	transition: all .3s ease;
}

.records .close-btn {
	border: none;
	font-size: 2rem;
	color: #fff;
	background-color: rgba(0,0,0,0.2);
	border-radius: 0 0 5px 0;
	position: absolute;
	top: 0;
	left: 0;
	width: 50px;
	height: 50px;
	cursor: pointer;
}

.records h2 {
	font-size: 2rem;
	margin: 10px 0;
}

.records li {
	font-size: 1.2rem;
	list-style: decimal;
}

.container {
	max-width: 1000px;
	margin: 0 auto;
	padding: 0 20px;
}

h1 {
	font-size: 3.5rem;
	color: #fff;
	line-height: 1.1;
	text-align: center;
	text-shadow: 2px 2px 7px rgba(0,0,0,0.7);
	padding: 20px 0;
}

.cards {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	user-select: none;
}

.card-outer {
	width: 130px;
	height: 180px;
	margin: 5px;
	perspective: 1000px;
}

.card-inner {
	position: absolute;
	width: 100%;
	height: 100%;
	transform-style: preserve-3d;
	transition: all 0.3s ease;
}

.front, .back {
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 5px;
	position: absolute;
	width: 100%;
	height: 100%;
	backface-visibility: hidden;
}

.front {
	background-color: #fff;
}

.back {
	background-color: #fff;
	transform: rotateY(180deg);
}

.back img {
	width: 100%;
	height: 100%;
}

.game {
	display: flex;
	justify-content: space-around;
	align-items: center;
	position: relative;
	padding: 5px 0;
	margin: 20px 0;
}

.buttons {
	display: flex;
}

.buttons button {
	border: none;
	font-size: 1.5rem;
	color: #fff;
	border-radius: 10px;
	background-color: rgba(0,0,0,0.3);
	padding: 5px 10px;
	margin: 0 10px;
	cursor: pointer;
	z-index: 15;
	transition: all .3s ease;
}

.timer {
	font-size: 1.5rem;
	color: #fff;
	border-radius: 10px;
	background-color: rgba(0,0,0,0.3);
	padding: 5px 10px;
	z-index: 15;
}

.flip {
	transform: rotateY(180deg);
}

.activeRecords {
	transform: translateX(0);
}